.SentFriendRequest {
  max-width: 300px;
  padding: 5px;

  border-radius: 10px;
  border: solid black 1px;

  display: flex;
  align-items: center;
  img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: solid black 1px;
  }
  p {
    padding-left: 5px;
    font-weight: bold;
  }
}
