.addNewFriend {
  padding-bottom: 15px;

  display: flex;
  flex-direction: column;
  align-items: center;
  label {
    font-weight: bold;
  }
  input {
    padding: 0 5px;
    width: fit-content;
    text-align: center;
    border-radius: 10px;
  }
  ul {
    .error {
      color: red !important;
      list-style: none;
    }
  }
}
